import { HttpClient } from '@angular/common/http';
import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { ModalController } from '@ionic/angular';
import { APIURL } from 'src/app/api-url';
import { ModalAcceptedPage } from '../modal-accepted/modal-accepted.page';

@Component({
  selector: 'app-cart',
  templateUrl: 'cart.page.html',
  styleUrls: ['cart.page.scss']
})
export class CartPage {

  cart = {
    products: [
      {
        title: 'Red Apple',
        subtitle: '1Kg',
        image: 'assets/images/apple.png',
        price: 4.99,
        quantity: 1
      },
      {
        title: 'Ranch Eggs',
        subtitle: '12pcs',
        image: 'assets/images/eggs.png',
        price: 0.29,
        quantity: 1
      },
      {
        title: 'Chicken',
        subtitle: '1Kg',
        image: 'assets/images/chicken.png',
        price: 1.99,
        quantity: 1
      },
      {
        title: 'Waiyu Beef',
        subtitle: '1Kg',
        image: 'assets/images/meat.png',
        price: 24.99,
        quantity: 1
      }
    ],
    subtotal: 244,
    taxRate: 12
  };
  customer_name: string;
  customer_email: string;
  product_id: string;
  customer_id: string;
  product:any;
  dataOrder: any;
  totalAmount: any;
  constructor(private modalCtrl: ModalController, private router: Router, public http: HttpClient) {
    this.isLogging();
    this.showCart();
  }

  async openModalAccepted() {
    const modal = await this.modalCtrl.create({
      component: ModalAcceptedPage,
      id: 'ModalAccepted',
    });
    return await modal.present();
  }

  isLogging(){
    this.customer_name = localStorage.getItem('customer_name');
    this.customer_email = localStorage.getItem('customer_email');
    console.log("Email: "+ this.customer_email);
    if(this.customer_name==null && this.customer_email==null){
      this.router.navigate(['/signup']);
    }
    else{
      return false;
    }
  }

  showCart(){
    let reqData = {
      "customer_id": localStorage.getItem("customer_id"),
      "token": "8abf4902a0db27dcb7f62a01c2fd0d0a"
    }
    this.http.post(APIURL.apiURL + 'list_order', reqData)
      .subscribe(data => {
        this.dataOrder = data['order'];
        console.log(this.dataOrder);
        this.totalAmount = data['total_amount'];
      });
  }
}
